<template>
  <v-app style="background:none">
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-card class="pa-4">
            <form>
              <!-- <v-icon large left>payment</v-icon> -->
              <span class="title font-weight-light">Simulador De Crédito</span>

              <v-select
                class="no-margin pt-2"
                v-model="tipoDeIngresoSeleccionado"
                :items="tiposDeIngreso"
                label="Tipo de Ingreso"
                color="#13D8F2"
              ></v-select>

              <v-text-field
                class="no-margin"
                v-model="ingresoMensual"
                label="Tu Ingreso Mensual"
                color="#13D8F2"
              ></v-text-field>
              <v-text-field
                class="no-margin"
                v-model="valorDelInmueble"
                @input="modificarMontoDeCredito = false, montoDeCreditoPorModificacion = undefined"
                label="Valor Del Inmueble"
                color="#13D8F2"
              ></v-text-field>
              <div @mouseover="infoMontoDeCredito = true" @mouseleave="infoMontoDeCredito = false">
                <div v-if="!modificarMontoDeCredito">
                  <v-text-field
                    class="no-margin"
                    v-model="montoDeCreditoMaximoFicitcio"
                    @focus="modificarMontoDeCredito = true"
                    label="Monto de Crédito"
                    append-icon="help"
                    color="#13D8F2"
                    :class="{'background-focus':infoMontoDeCredito}"
                  ></v-text-field>
                </div>
                <div v-else>
                  <v-text-field
                    class="no-margin"
                    v-model="montoDeCreditoPorModificacion"
                    @blur="validarModificacionDeMontoCredito"
                    label="Monto de Crédito"
                    append-icon="help"
                    color="#13D8F2"
                    :class="{'background-focus':infoMontoDeCredito}"
                  ></v-text-field>
                </div>
                <p
                  v-show="infoMontoDeCredito"
                  style="color:#13D8F2"
                >Si necesitas un credito menor, teclealo aquí</p>
              </div>

              <div :class="{'navbarFixed':navbarFixed}">
                <v-tabs v-model="tipoDePagoSeleccionado" grow slider-color="#13D8F2">
                  <v-tab style="font-size: 13px">Pagos Fijos</v-tab>
                  <v-tab style="font-size: 13px">Pagos Crecientes</v-tab>
                </v-tabs>

                <br>
                <br>
                <br>
                <v-bottom-nav :active.sync="plazoSeleccionado" :value="true" absolute>
                

                  <v-btn color="#13D8F2" flat value="2">
                    <span>Años</span>
                    <span class="number-years">20</span>
                  </v-btn>
                  <v-btn color="#13D8F2" flat value="1">
                    <span>Años</span>
                    <span class="number-years">15</span>
                  </v-btn>
                  <v-btn color="#13D8F2" flat value="0">
                    <span>Años</span>
                    <span class="number-years">10</span>
                  </v-btn>
                </v-bottom-nav>
              </div>
            </form>
          </v-card>
        </v-flex>
        <v-flex xs12 class="bancos-box">
          <div :key="index" v-for="(banco, index) in bancosPro">
            <v-card
              class="pa-4 mb-3" 
              style="padding-top: 0 !important;padding-bottom: 0 !important"
              v-if="banco.valores.disponibilidad"
              :class="{'f2f2f2':index % 2 == 0}"
            >
              <v-card-text class="pa-0 mb-0">
                <v-container grid-list-md text-xs-center pa-0>
                  <v-layout row wrap>
                    <v-flex xs12 md3>
                      <img
                        style="width:120px"
                        :src="require('@/assets/logos/'+imagenesDeBancos[banco.nombre])"
                      >
                    </v-flex>
                    <v-flex xs12 md6 class>
                      <div class="left-align">
                        <strong>
                          <span class="float-left">Pago Mensual:</span>
                          <span class="float-right">$ {{banco.valores.mensualidad.toFixed(2)}}</span>
                        </strong>
                        <br>

                        <div>
                          <span class="float-left">Ingresos:</span>
                          <span class="float-right">$ {{banco.valores.ingresos.toFixed(2)}}</span>
                        </div>
                        <br>

                        <div>
                          <span class="float-left">Tasa fija anual:</span>
                          <span class="float-right">{{(banco.valores.tasa * 100).toFixed(2)}} %</span>
                        </div>
                        <br>

                        <div>
                          <span class="float-left">Crédito Solicitado:</span>
                          <span class="float-right">
                            {{(banco.valores.porcentajeDeCredito * 100).toFixed(2)}} %
                            <span
                              class="pl-2"
                            >$ {{banco.valores.montoDeCredito.toFixed(2)}}</span>
                          </span>
                        </div>
                        <br>
                        <strong v-show="creditoPreautorizado">
                          <span class="float-left">Crédito Pre autorizado:</span>
                          <span class="float-right">$ {{creditoPreautorizado.toFixed(2)}}</span>
                        </strong>
                        <br>
                      </div>
                    </v-flex>
                    <v-flex xs12 md3 class>
                      <v-layout row wrap>
                    <v-flex xs6>
                      <v-btn
                        block
                        small
                        color="#13D8F2"
                        style="color:#333"
                        class="hover-invert"
                      >Informes</v-btn>
                    </v-flex>
                    <v-flex xs6>
                       <v-btn
                        block
                        small
                        color="#13D8F2"
                        style="color:#333"
                        class="hover-invert"
                      >Compartir</v-btn>
                      </v-flex>
                  </v-layout>
                     
                      <v-btn
                        block
                        small
                        color="#13D8F2"
                        style="color:#333"
                        class="hover-invert mt-0"
                      >Crédito En linea</v-btn>
                    </v-flex>
                  </v-layout>
                </v-container>

                <!-- <div style="text-align:left">
                  <div>Aforo: {{(banco.valores.porcentajeDeCredito * 100).toFixed(2)}} %</div>
                  <div>Credito: $ {{banco.valores.montoDeCredito.toFixed(2)}}</div>
                  <br>
                  <div>Mensualidad: $ {{banco.valores.mensualidad.toFixed(2)}}</div>
                  <div>Ingresos: $ {{banco.valores.ingresos.toFixed(2)}}</div>
                  <div>Tasa: {{(banco.valores.tasa * 100).toFixed(2)}} %</div>
                  <div
                    v-show="creditoPreautorizado"
                  >Crédito Preautorizado: $ {{creditoPreautorizado.toFixed(2)}}</div>
                  <hr> enganche: {{banco.valores.enganche}}
                </div>-->
              </v-card-text>
            </v-card>
          </div>
        </v-flex>
      </v-layout>

      <div
        is="hsbc"
        :tipo-de-ingreso="tipoDeIngresoSeleccionado"
        :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
        :valor-del-inmueble="valorDelInmueble"
        :plazo="plazoSeleccionado"
        :tipo-de-pago="tipoDePagoSeleccionado"
        :producto-seleccionado="productoSeleccionado"
      ></div>
      <div
        is="santander"
        :tipo-de-ingreso="tipoDeIngresoSeleccionado"
        :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
        :valor-del-inmueble="valorDelInmueble"
        :plazo="plazoSeleccionado"
        :tipo-de-pago="tipoDePagoSeleccionado"
        :producto-seleccionado="productoSeleccionado"
      ></div>
      <div
        is="banorte"
        :tipo-de-ingreso="tipoDeIngresoSeleccionado"
        :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
        :valor-del-inmueble="valorDelInmueble"
        :plazo="plazoSeleccionado"
        :tipo-de-pago="tipoDePagoSeleccionado"
        :producto-seleccionado="productoSeleccionado"
      ></div>
      <div
        is="scotiabank"
        :tipo-de-ingreso="tipoDeIngresoSeleccionado"
        :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
        :valor-del-inmueble="valorDelInmueble"
        :plazo="plazoSeleccionado"
        :tipo-de-pago="tipoDePagoSeleccionado"
        :producto-seleccionado="productoSeleccionado"
      ></div>
      <div
        is="banamex"
        :tipo-de-ingreso="tipoDeIngresoSeleccionado"
        :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
        :valor-del-inmueble="valorDelInmueble"
        :plazo="plazoSeleccionado"
        :tipo-de-pago="tipoDePagoSeleccionado"
        :producto-seleccionado="productoSeleccionado"
      ></div>
      <div
        is="banregio"
        :tipo-de-ingreso="tipoDeIngresoSeleccionado"
        :monto-de-credito-por-modificacion="montoDeCreditoPorModificacion"
        :valor-del-inmueble="valorDelInmueble"
        :plazo="plazoSeleccionado"
        :tipo-de-pago="tipoDePagoSeleccionado"
        :producto-seleccionado="productoSeleccionado"
      ></div>
    </v-container>
  </v-app>
</template>

<script>
import { mapState } from "vuex";

import Banamex from "./components/bancos/Banamex.vue";
import Banorte from "./components/bancos/Banorte.vue";
import Banregio from "./components/bancos/Banregio.vue";
import Hsbc from "./components/bancos/Hsbc.vue";
import Santander from "./components/bancos/Santander.vue";
import Scotiabank from "./components/bancos/Scotiabank.vue";

export default {
  name: "App",
  components: {
    Banamex,
    Banorte,
    Banregio,
    Hsbc,
    Santander,
    Scotiabank
  },
  data() {
    return {
      imagenesDeBancos: {
        banamex: "logo-banamex.gif",
        banorte: "logo-banorte.gif",
        banregio: "logo-banregio.gif",
        hsbc: "logo-hsbc.gif",
        santander: "logo-santander.gif",
        scotiabank: "logo-scotiabank.gif"
      },

      tiposDeIngreso: [
        "Asalariado",
        "Profesionista Independiente",
        "Independiente",
        "Socio de Empresa",
        "Negocio Propio"
      ],

      productoSeleccionado: "Comprar mi Casa",
      tipoDeIngresoSeleccionado: "",
      valorDelInmueble: "",
      valorDelCredito: 0,

      plazoSeleccionado: "2",
      tipoDePagoSeleccionado: 1,

      montoDeCreditoPorModificacion: undefined,
      modificarMontoDeCredito: false,

      ingresoMensual: "",

      infoMontoDeCredito: false,
      scroll: 0
    };
  },
  methods: {
    validarModificacionDeMontoCredito() {
      if (
        this.montoDeCreditoPorModificacion == undefined ||
        this.montoDeCreditoPorModificacion == "" ||
        this.montoDeCreditoPorModificacion == 0
      ) {
        this.modificarMontoDeCredito = false;
        this.montoDeCreditoPorModificacion = undefined;
      } else {
      }
    },
    scrollUpdate() {
      this.scroll = window.scrollY;
    }
  },
  computed: {
    navbarFixed() {
      if (this.scroll >= 337) {
        return true;
      } else {
        return false;
      }
    },
    bancosPro() {
      return this.bancos.sort((a, b) => {
        return (
          (a.valores.mensualidad) - (b.valores.mensualidad)
        );
      });
    },
    creditoPreautorizado() {
      if (this.tipoDeIngresoSeleccionado == "Asalariado") {
        return parseFloat(this.ingresoMensual) * 50;
      } else {
        return parseFloat(this.ingresoMensual) * 36;
      }
    },

    montoDeCreditoMaximoFicitcio() {
      return this.valorDelInmueble * 0.9;
    },

    ...mapState(["bancos"])
  },
  mounted() {
    setInterval(() => {
      this.$forceUpdate();
    }, 10);

    window.addEventListener("scroll", this.scrollUpdate, false);
  }
};
</script>
<style>
.number-years {
  font-size: 30px;
}

.background-focus * {
  background: none !important;
}
.background-focus .v-input__slot,
.background-focus .v-icon {
  border-color: rgba(19, 216, 242, 0.486) !important;
  color: rgba(19, 216, 242, 0.486) !important;
}
.v-bottom-nav {
  box-shadow: none !important;
}
.left-align {
  text-align: left;
}
.float-left {
  float: left;
}
.float-right {
  float: right;
}

.hover-invert:hover {
  background: #333 !important;
  color: #13d8f2 !important;
}
.no-margin {
  margin: 0;
  padding: 0;
}

.navbarFixed {
  position: fixed;
  z-index: 1000;
  top: 0;
  background: #fff;
  width: 100%;
  left: 0;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
}

.f2f2f2 {
  background: #f2f2f2 !important
}
</style>
